import React from "react";
import Carousel from "react-spring-3d-carousel";
import uuidv4 from "uuid";
import { config } from "react-spring";
import "./styles.css";
import slider1 from "./images/slider1.png";
import slider2 from "./images/slider2.png";
import slider3 from "./images/slider3.png";

export default class Portfolio extends React.Component {
  slides = [
    {
      key: uuidv4(),
      content: <img src={slider1} alt="1" />
    },
    {
      key: uuidv4(),
      content: <img src={slider2} alt="2" />
    },
    {
      key: uuidv4(),
      content: <img src={slider3} alt="3" />
    },
    {
      key: uuidv4(),
      content: <img src={slider1} alt="4" />
    },
    {
      key: uuidv4(),
      content: <img src={slider2} alt="5" />
    },
    {
      key: uuidv4(),
      content: <img src={slider3} alt="6" />
    },
    {
      key: uuidv4(),
      content: <img src={slider1} alt="7" />
    },
    {
      key: uuidv4(),
      content: <img src={slider2} alt="8" />
    }
  ].map((slide, index) => {
    return { ...slide, onClick: () => this.setState({ goToSlide: index }) };
  });

  onChangeInput = (e) => {
    this.setState({
      [e.target.name]: parseInt(e.target.value, 10) || 0
    });
  };

  state = {
    goToSlide: 0,
    offsetRadius: 2,
    showNavigation: true,
    config: config.gentle,
    autoplay: true,
    autoplaySpeed: 2000
  };

  render() {
    const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 3,
      slidesToScroll: 1
    };

    return (
      <div style={{ width: "80%", height: "25rem", margin: "0 4.5rem" }}>
        <Carousel
          slides={this.slides}
          goToSlide={this.state.goToSlide}
          offsetRadius={this.state.offsetRadius}
          animationConfig={this.state.config}
          autoPlay
          interval={this.state.autoplaySpeed}
          infiniteLoop={true}
          dynamicHeight={true}
          ref={(slider) => (this.slider = slider)}
          {...settings}
        ></Carousel>
      </div>
    );
  }
}
