import "./styles.css";
import Mainpage from "./homepage";
import Mainabout from "./aboutus";
import Mainservices from "./services";
import Maincontact from "./contactus";
import { Switch, Route } from "react-router-dom";
const Aboutus = () => {
  return <Mainabout />;
};

const Services = () => {
  return <Mainservices />;
};

const Contactus = () => {
  return <Maincontact />;
};

export default function App() {
  return (
    <Switch>
      <Route exact path="/">
        <Mainpage />
      </Route>
      <Route path="/aboutus">
        <Aboutus />
      </Route>
      <Route path="/services">
        <Services />
      </Route>

      <Route path="/Contactus">
        <Contactus />
      </Route>
    </Switch>
  );
}
