class Maincontact extends React.Component {
  render() {
    return (
      <div className="App">
        <Menu />

        <section className="Contactmain">
          <div className="container">
            <div className="row inner-adjust">
              <div className="col">
                <img src={ctimg} alt="" />
              </div>

              <div className="col viewadjust">
                <h1 className="custom">
                  Have a project
                  <br />
                  in mind? Why not start it with a talk
                </h1>
                <p>Contact with our expert team about your query or project</p>

                <form>
                  <div className="form-group">
                    <input
                      type="name"
                      className="frm-ctl"
                      placeholder="Enter name"
                    ></input>
                  </div>

                  <div className="form-group">
                    <input
                      type="Phone"
                      className="frm-ctl"
                      placeholder="Enter phone"
                    ></input>
                  </div>

                  <div className="form-group">
                    <input
                      type="email"
                      className="frm-ctl"
                      placeholder="Enter email"
                    ></input>
                  </div>

                  <div className="form-group">
                    <textarea
                      type="Enter message"
                      className="frm-txt"
                      placeholder="Enter message"
                    ></textarea>
                  </div>

                  <button type="submit" className="btn btn-primary">
                    Let’s get conntected <img src={icon} alt="" />
                  </button>
                </form>
              </div>
            </div>
          </div>
        </section>
        <Footer />
      </div>
    );
  }
}

